
import XProgress from 'vux/src/components/x-progress/index.vue'

import tabGroup from '@/components/group/tab'
export default {
data () {
return {
shu: {},
title: '限时抢购',
list: [
{
time: '19:00',
title: '抢购中'
},
{
time: '21:00',
title: '抢购中'
},
{
time: '23:00',
title: '抢购中'
},
{
time: '07/12',
title: '明日预告'
},
{
time: '21:00',
title: '抢购中'
}
],
seckilllist: []
}
},
components: {
XProgress,
tabGroup: tabGroup
},
mounted () {
this.getseckilllist()
},
methods: {
getback () {
history.go(-1)
},
hdindex (item) {
this.shu = item
},
getseckilllist () {
this.$http.get('sumuji-server/seckill/getSeckillList', {params: {
pageSize: 5, pageNum: 1
}}).then(res => {
this.seckilllist = res.data.data
})
},
pintuangoodsneir () {
this.$router.push('./pintuangoodsneir')
}
}
}



